<template>
  <div class="setting">
    <el-form
      :inline="true"
      :model="setting"
      @submit.native.prevent="onSubmit"
      class="form-search"
      label-width="160px"
      v-loading="loading"
    >
      <div class="searchs">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="好友投资一级比例">
              <el-input v-model="setting.top1_apr" size="small" placeholder="请输入比例">
                <template slot="append">%</template>
              </el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="好友投资二级比例">
              <el-input v-model="setting.top2_apr" size="small" placeholder="请输入比例">
                <template slot="append">%</template>
              </el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="好友投资三级比例">
              <el-input v-model="setting.top3_apr" size="small" placeholder="请输入比例">
                <template slot="append">%</template>
              </el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="是否开启限制">
              <el-select size="small" v-model="setting.is_disable" clearable placeholder="请选择">
                <el-option value="Y" label="是" />
                <el-option value="N" label="否" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="达树成长值显示">
              <el-select size="small" v-model="setting.open_tree_type" clearable placeholder="请选择">
                <el-option :value="k" :label="v.name" v-for="(v, k) in setting.tree" :key="k" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="是否开启推荐用户显示">
              <el-select size="small" v-model="setting.is_top_open" clearable placeholder="请选择">
                <el-option value="Y" label="是" />
                <el-option value="N" label="否" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="签到次数开启理财">
              <el-input v-model="setting.signin_num" size="small" placeholder="请输入次数">
                <template slot="append">次</template>
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
      <el-col>
        <el-form-item>
          <el-button type="primary" size="small" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'

export default {
  data() {
    return {
      loading: false,
      setting: {},
    }
  },
  mounted() {
    this.loading = true
    Fetch2('/sys/config/infoitemdq')
      .then(r => {
        this.setting = r.data
        this.loading = false
      })
      .catch(() => (this.loading = false))
  },
  methods: {
    onSubmit() {
      this.loading = true
      Fetch3('/sys/config/infoitemdq', {
        ...this.setting,
      })
        .then(() => {
          Message.success('更新成功')
          this.loading = false
        })
        .catch(() => {
          Message.error('更新失败')
          this.loading = false
        })
    },
  },
}
</script>
<style lang="less" scoped>
.setting {
  > div {
    padding: 14px 14px 14px 15px;
  }

  .searchs {
    background-color: #f2f2f2;
    border-left: 5px solid #1aa094;
    color: #606266;
    font-size: 16px;
  }

  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
